import * as C from '../types';
import { get } from '../api';

export const getTechList = () => async (dispatch, getState, api) => {
	dispatch({
		type: C.FETCH_TECH_LIST,
	});
	get('/technology/getList')
		.then(({ data }) => {
			dispatch({
				type: C.FINISHED_FETCH_TECH_LIST,
				data,
			});
		})
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_TECH_LIST,
				error,
			});
		});
};

